import React, {Component} from 'react';
import LoadingScreen from '../ux/LoadingScreen';

export default class EmbeddedLoader extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loading:     true,
      id:          this.props.gist.split('/')[1],
      src:         this.props.gist,
      file:        this.props.file || null,
      stylesheet:  null,
      description: '',
      content:     ''
    };
  }

  addCallback() {
    window[`callback_${this.state.id}`] = function (gist) {
      this.setState({
        loading:     false,
        content:     `<h2>${gist.description}</h2>${gist.div.replace(/href=/g, 'target="_blank" href=')}`,
        description: gist.description
      });
      this.props.addStylesheet(gist.stylesheet);
    }.bind(this);
  }

  componentDidMount() {
    const script = document.createElement('script');
    let url = `https://gist.github.com/${this.state.src}.json?callback=callback_${this.state.id}`;
    if (this.state.file) url += `&file=${this.state.file}`;
    this.addCallback();
    script.type = 'text/javascript';
    script.src = url;
    document.head.appendChild(script);
  }

  render() {
    if (this.state.loading) {
      return <LoadingScreen/>
    } else {
      return <article dangerouslySetInnerHTML={{__html: this.state.content}}/>
    }
  }
}



