import React, {Component} from 'react';
import EmbeddedLoader from './EmbeddedLoader';

export default class Guides extends Component {
  constructor(props) {
    super(props);

    this.state = {
      existingStylesheets: []
    };

    this.addStylesheet = this.addStylesheet.bind(this);
  }

  addStylesheet(href) {
    let exists = false;
    this.state.existingStylesheets.forEach(existingStylesheet => {
      if (href === existingStylesheet) exists = true;
    });
    if (!exists) {
      let stylesheet = document.createElement('link');
      stylesheet.type = 'text/css';
      stylesheet.rel = 'stylesheet';
      stylesheet.href = href;
      document.head.appendChild(stylesheet);

      this.setState({
        existingStylesheets: [...this.state.existingStylesheets, href]
      });
    }
  }

  render() {
    return (
      <article className="guides">
        <EmbeddedLoader gist="msaracevic/7a315f610c11d39aeea55061d3b9bf05" addStylesheet={this.addStylesheet}/>
        <EmbeddedLoader gist="msaracevic/5d757e2fc72482a9a4a439969500c2eb" addStylesheet={this.addStylesheet}/>
      </article>
    );
  }
}



